.contact {
    display: contents;
    max-height: 100%;
    min-height: 100%;
}
.contact-body {
    max-height: 100%;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}
.contact-body__loader {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.user-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: -5px;
}

.none-user {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    line-height: 72%;
    letter-spacing: 0.01em;
    color: #0d1c2e;
    font-weight: 600;
    -webkit-user-select: none;
    user-select: none;
    -webkit-user-drag: none;
}
